body {
  /**

    我的页面
    */
}
body .auth-header {
  padding: 35px 0;
}
body .auth-header .auth-title {
  text-align: center;
  font-size: 34px;
  color: #3cc51f;
  font-weight: 400;
  margin: 0 15%;
}
body .auth-header .auth-sub-title {
  text-align: center;
  color: #888;
  font-size: 14px;
}
body .auth-header .auth-second-title {
  text-align: center;
  font-size: 24px;
  color: #3cc51f;
  font-weight: 400;
  margin: 0 15%;
}
body .help-block {
  color: red;
}
body .login .login-error {
  justify-content: center;
  align-items: center;
}
body .login .login-submit {
  width: 80%;
  margin: 5px auto;
}
body .login .login-forget {
  position: absolute;
  right: 10%;
  bottom: 0;
  padding: 0;
  margin: 0;
}
body .login .login-forget .weui_btn_primary {
  margin: 0px;
}
body .login .login-row {
  padding: 10px 10%;
  position: relative;
  display: flex;
  align-items: center;
}
body .register .login-error {
  justify-content: center;
  align-items: center;
}
body .register .login-submit {
  width: 80%;
  margin: 5px auto;
}
body .register .login-row {
  padding: 10px 10%;
  position: relative;
  display: flex;
  align-items: center;
}
body div.swiper-container {
  width: 100%;
  height: 150px;
}
body div.swiper-container .swiper-slide img {
  width: 100%;
  height: 150px;
}
body div.category {
  width: 100%;
  display: block;
  overflow-x: auto;
  white-space: nowrap;
  border-bottom: 1px solid #eee;
  padding: 10px 0;
}
body div.category .category_item {
  display: inline-block;
  margin: 0 2px;
  padding: 2px 10px;
  font-size: 15px;
  background: white;
}
body div.category .category_item a {
  white-space: nowrap;
  margin-left: 5px;
  font-size: 15px;
  color: black;
}
body div.category .category_item_active {
  background: #04BE02;
  border-radius: 8px;
}
body div.category .category_item_active a {
  color: white;
}
body div.home_container {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-bottom: 100px;
}
body div.home_container div.left {
  width: 20%;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #eee;
}
body div.home_container div.left div.category_item {
  text-align: center;
  border-bottom: 1px solid #eee;
  background: white;
}
body div.home_container div.left div.category_item a {
  font-size: 10px;
  color: black;
}
body div.home_container div.left div.category_item_active {
  background: #04BE02;
}
body div.home_container div.left div.category_item_active a {
  font-size: 12px;
  color: white;
}
body div.home_container div.right {
  width: 80%;
}
body div.home_container div.right .good_item {
  margin: 0;
  width: 100%;
}
body div.home_container div.right .good_item .weui_media_hd {
  margin-right: 3px;
}
body div.home_container div.right .good_item .weui_media_bd {
  min-width: 0;
  width: 60%;
}
body div.home_container div.right .good_item .weui_media_bd .weui_media_title {
  font-size: 15px;
}
body div.home_container div.right .good_item .cart {
  display: block;
  position: absolute;
  text-align: center;
  right: 5px;
  top: 50%;
  border: 1px solid #eee;
  box-sizing: content-box;
  transform: translate(0, -50%);
}
body div.home_container div.right .good_item .cart a {
  display: inline-block;
  margin: 0;
  padding: 0 6px;
  text-align: center;
  color: #aaa;
}
body div.home_container div.right .good_item .cart span {
  display: inline-block;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  margin: 0;
  padding: 0 10px;
  text-align: center;
  color: #04BE02;
}
body div.home_container div.right .market-price {
  text-decoration: line-through;
  color: #aaa;
}
body div.home_container div.right .shop-price {
  font-size: 15px;
  font-weight: bold;
  color: red;
}
body div.home_container div.right .weui_media_box {
  padding: 5px;
}
body div.home_container div.right .summary {
  font-size: 12px;
}
body div.weui_tabbar {
  width: 100%;
  height: 44px;
  position: fixed;
  bottom: 0;
  left: 0;
}
body div.weui_tabbar .weui_tabbar_item {
  padding-top: 2px;
}
body div.weui_tabbar .weui_tabbar_label {
  margin-top: 2px;
}
body div.shopping {
  display: block;
  position: relative;
}
body div.shopping div.shopping-cart {
  display: block;
  position: fixed;
  width: 40px;
  height: 40px;
  bottom: 60px;
  left: 15px;
  border-radius: 20px;
  background: #04BE02 url('/images/shopping_cart.png') no-repeat center;
  background-size: 30px 30px;
  z-index: 100;
}
body div.shopping div.shopping-cart .shopping-cart-count {
  position: absolute;
  display: block;
  width: 0.9em;
  height: 0.9em;
  line-height: 0.9em;
  text-align: center;
  background: red;
  border-radius: 50%;
  top: 6px;
  right: 4px;
  color: white;
  font-size: 0.9em;
  z-index: 100;
}
body div.shopping .shopping-cart-detail {
  display: none;
  position: fixed;
  border-radius: 10px;
  bottom: 90px;
  left: 10%;
  width: 80%;
  background: rgba(200, 200, 200, 0.8);
  padding: 5px;
  margin: 0;
  z-index: 1;
}
body div.shopping .shopping-cart-detail .amount {
  color: red;
}
body div.shopping .shopping-cart-detail .total {
  position: absolute;
  right: 0;
  margin-right: 10px;
  color: red;
}
body div.shopping .shopping-cart-detail .cart-button {
  border-top: 1px solid #d9d9d9;
  display: inline-block;
  width: 100%;
  text-align: center;
}
body .shopping-cart-check {
  border-radius: 10px;
  width: 80%;
  background: rgba(200, 200, 200, 0.8);
  padding: 5px;
  margin: 10px auto;
  z-index: 1;
}
body .shopping-cart-check .amount {
  color: red;
}
body .shopping-cart-check .total {
  position: absolute;
  right: 0;
  margin-right: 10px;
  color: red;
}
body .shopping-cart-check .cart-button {
  border-top: 1px solid #d9d9d9;
  display: inline-block;
  width: 100%;
  text-align: center;
}
body .check-header {
  padding: 35px 0;
}
body .check-header .check-title {
  text-align: center;
  font-size: 34px;
  color: #3cc51f;
  font-weight: 400;
  margin: 0 15%;
}
body .profile .profile_top {
  display: block;
  position: relative;
  width: 100%;
  height: 200px;
  background: #04BE02;
  color: white;
}
body .profile .profile_top .profile-row {
  position: relative;
}
body .profile .profile_top .profile-row a {
  margin: 0;
}
body .profile .profile_top .profile-row .right {
  position: absolute;
  right: 0;
}
body .profile .profile_top .avatar {
  position: absolute;
  width: 80px;
  height: 64px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
body .profile .profile_top .avatar .avatar_image {
  margin: auto;
  width: 64px;
  height: 64px;
  border-radius: 50%;
}
body .profile .profile_top .avatar a {
  display: inline-block;
  width: 100%;
  text-align: center;
  border-radius: 0;
  border: transparent;
  color: white;
}
body .profile .order {
  display: flex;
  width: 100%;
  border-bottom: solid 1px #ddd;
}
body .profile .order .item {
  flex: 1;
  padding: 5px 0;
  border: 0;
  border-right: solid 1px #ddd;
}
body .profile .order .item .weui_tabbar_label {
  color: #04BE02;
  font-size: 15px;
}
body div.profile-container {
  margin-top: 50px;
}
body div.profile-container .weui_panel_bd {
  padding-top: 1px;
}
